/**
 * PromoPopup.js
 * popup used to display promos on Form landings
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import $ from 'jquery';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Row, Col, Tooltip } from 'reactstrap';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';

import TooltipImg from 'assets/images/forms/Tooltip.svg';
// import LabelImg from 'assets/images/forms/Label.svg';
import ClockImg from 'assets/images/forms/Clock.svg';
import ProductImages from 'assets/images/ProductPage';

import VoucherTag from 'components/Templates/VoucherTag/VoucherTag';
import InsurersLogo from 'assets/images/InsurerLogos';
import Button from 'components/FormsV3/Button/Button';
import TabView from 'components/Templates/TabView/TabViewNew';
import RestrictionsPopup from '../RestrictionsPopup/RestrictionsPopup';
import FlowEnquiry from '../FlowEnquiry/FlowEnquiry';

import { getProductDetails } from '../../../actions/insuranceActions';
import { setRestrictionPopup } from '../../../actions/restrictionsActions';

import { gaTag } from '../../../lib/tagFunctions';

import './ProductDetails.scss';

class ProductDetails extends Component {
  constructor(props) {
    super(props);

    this.state = {
      productData: null,
      showTooltips: {},
      showModal: false,
      showPrexModal: false,
      isShowBottomPrice: false,
      emailPopupData: null,
    };

    this.handleOnClick = this.handleOnClick.bind(this);
    this.handleRestrictions = this.handleRestrictions.bind(this);
    this.getNewData = this.getNewData.bind(this);

    this.showModal = this.showModal.bind(this);
    this.hideModal = this.hideModal.bind(this);

    this.onMovement = this.onMovement.bind(this);

    this.showEnquiryModal = this.showEnquiryModal.bind(this);
  }

  async componentDidMount() {
    const { form, splitParams } = this.props;

    this.getNewData();

    this.hideInterval = setInterval(() => {
      if (!_.isEmpty($('#intercom-container'))) {
        $('#intercom-container').hide();
        clearInterval(this.hideInterval);
      }
    }, 10);

    window.addEventListener('scroll', this.onMovement);
    window.addEventListener('resize', this.onMovement);

    this.onMovement();

    if (splitParams && splitParams.product && splitParams.insurer) {
      gaTag({
        page_title: 'Travel - Product Page',
        page_path: `/${form}/plan/${splitParams.insurer}/${splitParams.product}`,
      });
    }

    window.scrollTo(0, 0);
  }

  async componentDidUpdate(prevProps) {
    const { plansData } = this.props;
    if (!_.isEqual(plansData, prevProps.plansData)) {
      this.getNewData();
    }
  }

  componentWillUnmount() {
    this.setState({
      isShowBottomPrice: false,
    });

    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }

    clearInterval(this.hideInterval);

    window.removeEventListener('scroll', this.onMovement);
    window.removeEventListener('resize', this.onMovement);
  }

  onMovement() {
    if (this.bannerDOM) {
      const { isShowBottomPrice } = this.state;

      if (window.innerWidth < 768 && !isShowBottomPrice) {
        this.setState({
          isShowBottomPrice: true,
        });
      } else if (window.innerWidth >= 768) {
        const scrollY = this.bannerDOM.getBoundingClientRect().y;
        if (
          (scrollY < 0 && !isShowBottomPrice) ||
          (scrollY >= 0 && isShowBottomPrice)
        ) {
          this.setState({
            isShowBottomPrice: scrollY < 0,
          });
        }
      }
    }
  }

  async getNewData() {
    const {
      splitParams,
      parseForm,
      values,
      setOverridePlanTypes,
      plansData,
      handleAPI,
      switchView,
    } = this.props;

    if (splitParams && splitParams.product && splitParams.insurer) {
      let productData = plansData && _.find(plansData.insurances_all, data => data && data.info && data.info.insurer_slug === splitParams.insurer && data.info.product_slug === splitParams.product);
      if (!productData) {
        const requestData = parseForm(values);
        productData = await handleAPI(
          () => getProductDetails(splitParams.insurer, splitParams.product, requestData),
          false,
          'Unable to get product data! Please try again.',
          switchView,
        );
      }

      if (productData) {
        setOverridePlanTypes(_.map(productData.policy_types, type => ({ display_name: _.startCase(type), value: _.startCase(type) })));

        this.setState({
          productData,
        });

        return;
      }

      // Cannot get data, so show error
      this.showModal();
    }
  }

  showEnquiryModal(showPrexModal) {
    this.setState({ showPrexModal });
  }

  showModal() {
    const { showModal } = this.state;

    if (!showModal) {
      this.setState({ showModal: true });
    }
  }

  hideModal() {
    const { switchView } = this.props;
    this.setState({ showModal: false });
    switchView();
  }

  handleOnClick() {
    const { productData } = this.state;
    const { handleOnClick, doSetRestrictionPopup, emailPopupData } = this.props;

    try {
      for (let i = 0; i < emailPopupData.length; ++i) {
        if (productData.info.insurer_product_slug === emailPopupData[i].insurer_product_slug) {
          this.setState({
            emailPopupData: emailPopupData[i],
            showPrexModal: true,
          });
          return;
        }
      }
      handleOnClick(productData, doSetRestrictionPopup);
    } catch (error) {
      // eslint-disable-next-line
      // Cannot proceed to next flow, show error
      this.showModal();
    }
  }

  handleRestrictions(callback) {
    const { productData } = this.state;
    const {
      doSetRestrictionPopup,
    } = this.props;

    doSetRestrictionPopup({
      values: productData.restrictions,
      clickCallback: callback,
      // doAcceptTag: () => ampTag(`${_.startCase(category)}AcceptA`),
    });
  }

  renderHeader(isRespBannerActive) {
    const { productData } = this.state;
    const { planType, values } = this.props;

    try {
      const logoImage = InsurersLogo[productData.info.insurer_slug];
      const pricePlan = productData.product_details.coverages[planType].premium;

      const cashback = `$${(productData.info.cashback * pricePlan.net_price).toFixed(2)}`;
      let discount = null;
      let price = null;

      if (pricePlan.net_price !== pricePlan.price) {
        const tripType = values.trip_type.value === 'Single Trip' ? 'single' : 'annual';
        discount = productData.info.discount[tripType] ? productData.info.discount[tripType].display : productData.info.discount.display;
        if (!discount || discount === '') {
          discount = null;
        }
        // eslint-disable-next-line
        price = pricePlan.price;
      }

      return (
        <Fragment>
          <Col
            className={[!isRespBannerActive ? 'd-flex' : 'd-none d-md-flex', 'product-header-container-wrapper'].join(' ')}
            xs={12}
          >
            <div
              className="product-header-container"
              ref={node => this.bannerDOM = node}
            >
              <div className="product-logo">
                <img src={logoImage} alt={`${productData.info.insurer_slug}.svg`} />
              </div>
              <div className="product-name-container">
                <h2>{productData.info.product_name}</h2>
                {
                  discount &&
                  <div className={`discount-container ${!discount && 'hidden'}`}>
                    <p>{discount} Discount</p>
                  </div>
                }
                {
                  productData.info.teaser &&
                  <VoucherTag
                    className={discount ? 'tag-label-margin' : ''}
                    teaser={productData.info.teaser}
                  />
                }
              </div>
            </div>
            <div className="price-container d-none d-md-flex">
              <div className="price-inner-container">
                <div className="price-value-container">
                  <h2 className="net-price">${pricePlan.net_price.toFixed(2)}</h2>
                  {
                    price ?
                      <p className="price"><span>${price.toFixed(2)}</span></p>
                    :
                      null
                  }
                </div>
                <div className="button-container">
                  <Button
                    variant="medium-flat-green"
                    height={48}
                    onClick={this.handleOnClick}
                  >
                    <p>Buy Policy</p>
                  </Button>
                  <div className="cashback-earned-container">
                    {/* <img src={LabelImg} alt="Label.svg" /> */}
                    <span>Earn {cashback} Cashback</span>
                  </div>
                </div>
              </div>
            </div>
          </Col>
        </Fragment>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderBanner() {
    const { productData } = this.state;
    const { splitParams } = this.props;

    try {
      const logoImage = InsurersLogo[productData.info.insurer_slug];
      const bannerImg = productData.product_details.banner_img;
      const bannerRespImg = productData.product_details.banner_mobile_img;
      const isXploraJapan = splitParams && splitParams.insurer === 'tokio_marine' && splitParams.product === 'xplora_japan';

      return (
        <Col className="banner-container" xs={12}>
          <div className="banner">
            {bannerImg && <img className="d-none d-md-block" src={bannerImg} alt="productBanner.svg" />}
            {bannerRespImg && <img className="d-block d-md-none" src={bannerRespImg} alt="productBanner.svg" />}
          </div>
          {
            bannerRespImg &&
            <div className="bottom-banner">
              <div className="banner-logo d-flex d-md-none">
                <img src={logoImage} alt={`${productData.info.insurer_slug}.svg`} />
              </div>
              { isXploraJapan && <h5>*This plan covers only Japan</h5> }
            </div>
          }
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderFeature() {
    const { productData } = this.state;

    try {
      if (!productData.product_details.features) {
        return null;
      }

      return (
        <Col xs={12} className="segment">
          <Row noGutters>
            {
              _.map(productData.product_details.features, (feature, index) => (
                <Col
                  key={`feature-info-${index}`}
                  xs={12}
                  md={4}
                  className="feature-container"
                >
                  <div className="image">
                    <img src={feature.icon_img} alt={`tm-info-${index}.svg`} />
                  </div>
                  <div className="desc">
                    <h4>{feature.title}</h4>
                    <p>{feature.description}</p>
                  </div>
                </Col>
              ))
            }
          </Row>
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderHighlights() {
    const { productData, showTooltips } = this.state;
    const { planType } = this.props;

    try {
      return (
        <Col xs={12} className="segment d-none d-md-flex">
          <Row className="w-100" noGutters>
            {
              _.reduce(productData.product_details.coverages[planType].highlights, (result, highlight, index) => {
                if (index < 3) {
                  const id = `highlight-info-tooltip-${index}`;
                  result.push((
                    <Col
                      key={`highlight-info-${index}`}
                      xs={4}
                      className="highlight-container"
                    >
                      <Row className="d-flex justify-content-center h-100" noGutters>
                        <div className="d-flex flex-column">
                          <div className="highlight-title-container">
                            <div className="title">
                              {highlight.title}
                            </div>
                            <div className="highlight-tooltip">
                              <Tooltip
                                target={id}
                                isOpen={showTooltips[id]}
                                toggle={() => this.setState({ showTooltips: { ...showTooltips, [id]: false } })}
                                className="animated fade"
                              >
                                {highlight.tooltip}
                              </Tooltip>
                              <img
                                className={`${!highlight.tooltip && 'tooltip-hidden'}`}
                                id={id}
                                src={TooltipImg}
                                alt="Tooltip.svg"
                                onMouseEnter={() => { if (highlight.tooltip) this.setState({ showTooltips: { ...showTooltips, [id]: true } }); }}
                              />
                            </div>
                          </div>
                          <div className="d-flex justify-content-left align-items-end h-100">
                            <p className="value">{highlight.value}</p>
                          </div>
                        </div>
                      </Row>
                    </Col>
                  ));
                }

                return result;
              }, [])
            }
          </Row>
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderValue(data) {
    if (data.type === 'boolean') {
      return <i className={`fa ${data.value === 'Yes' ? 'fa-check' : 'fa-times'}`} />;
    }

    return data.value;
  }

  renderInnerDetails(coverage, index, borderStyle = '') {
    return (
      <Row className={`content-container ${borderStyle}`} noGutters>
        {
          _.map(coverage.contents, (content, index2) => {
            const isContentAvaliable = content && content.value;
            const bottomPaddingStyle = index2 !== coverage.contents.length - 1 ? 'content-sub-container-padding' : '';

            return (
              <Fragment key={`coverage-detail-${index}-${index2}`}>
                {
                  isContentAvaliable &&
                  <Col
                    className={`content ${!content.sub_contents && content.type !== 'url' ? 'content-padding' : ''}`}
                    xs={12}
                  >
                    <Row className="w-100" noGutters>
                      <Col xs={6}>
                        <p className="title content-bold">{content.title}</p>
                      </Col>
                      <Col xs={6}>
                        {
                          content.type === 'url' ?
                            <a
                              className="content-bold"
                              href={content.value}
                              target="__blank"
                            >
                              Read
                            </a>
                          :
                            <p className="content-bold">{this.renderValue(content)}</p>
                        }
                      </Col>
                    </Row>
                  </Col>
                }
                {
                  content.sub_contents && (
                    <Col xs={12}>
                      <Row className={`content-sub-container w-100 ${bottomPaddingStyle}`} noGutters>
                        {
                          _.map(content.sub_contents, (sub, index3) => (
                            <Col
                              xs={12}
                              key={`coverage-detail-${index}-${index2}-${index3}`}
                            >
                              <Row className="content-sub w-100" noGutters>
                                <Col xs={6}>
                                  <p className="title">{sub.title}</p>
                                </Col>
                                <Col xs={6}>
                                  {
                                    sub.type === 'url' ?
                                      <a
                                        href={sub.value}
                                        target="__blank"
                                      >
                                        read
                                      </a>
                                    :
                                      <p>{this.renderValue(sub)}</p>
                                  }
                                </Col>
                              </Row>
                            </Col>
                          ))
                        }
                      </Row>
                    </Col>
                  )
                }
                {
                  index2 !== coverage.contents.length - 1 && <Col className="d-block d-md-none content-break" xs={12} />
                }
              </Fragment>
            );
          })
        }
      </Row>
    );
  }

  renderDetails(isPrex = false) {
    const { productData } = this.state;
    const { planType } = this.props;

    try {
      const coverages = [...(isPrex ? productData.product_details.coverages[planType].prex_coverages : productData.product_details.coverages[planType].coverages)];
      if (_.isEmpty(coverages)) {
        return null;
      }

      if (!isPrex && productData.product_details.policy_documents) {
        coverages.push(productData.product_details.policy_documents);
      }

      return (
        <Col xs={12} className="d-none d-md-block segment coverage-container">
          { isPrex && <p className="prex-text">Sub-limit claims relating to your pre-existing medical conditions</p> }
          {
            _.map(coverages, (coverage, index) => {
              const isPolicyDocuments = _.lowerCase(coverage.header) === 'policy documents';
              // eslint-disable-next-line
              const borderStyle = index === coverages.length - 1 ? isPolicyDocuments ? '' : 'no-bottom-padding' : 'content-border';
              const image = ProductImages[coverage.header] || ClockImg;

              return (
                <Row
                  key={`coverage-detail-${index}`}
                  className="coverage-inner-container"
                  noGutters
                >
                  <Col className="coverage-header" xs={4}>
                    <img src={image} alt="clockImg.svg" />
                    <h5 className="content-bold">{coverage.header}</h5>
                  </Col>
                  <Col xs={8}>
                    {this.renderInnerDetails(coverage, index, borderStyle)}
                  </Col>
                </Row>
              );
            })
          }
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderDetailsResp(isPrex) {
    const { productData } = this.state;
    const { planType } = this.props;

    try {
      const coverages = [...(isPrex ? productData.product_details.coverages[planType].prex_coverages : productData.product_details.coverages[planType].coverages)];
      if (_.isEmpty(coverages)) {
        return null;
      }

      const JSX = _.map(coverages, (coverage, index) => {
        const image = ProductImages[coverage.header] || ClockImg;

        return (
          <Fragment key={`coverage-resp-${index}`}>
            <Col
              className="coverage-resp-container"
              xs={12}
            >
              <Row noGutters>
                <Col xs={12}>
                  { isPrex && <p className="prex-text">Sub-limit claims relating to your pre-existing medical conditions</p> }
                </Col>
                <Col className="coverage-resp-main-title" xs={12}>
                  <div className="coverage-resp-img">
                    <img src={image} alt="coverage-resp.svg" />
                  </div>
                  <h4>{coverage.header}</h4>
                </Col>
              </Row>
              {this.renderInnerDetails(coverage, index)}
            </Col>
          </Fragment>
        );
      });

      const isTabView = JSX && JSX.length > 1;
      let tabs = null;
      if (isTabView) {
        tabs = _.reduce(coverages, (result, coverage) => {
          result.push({ desc: coverage.header });
          return result;
        }, []);
      }

      return (
        <Col className="d-flex d-md-none mb-4 flex-column" xs={12}>
          {
            isTabView ?
              <TabView
                style={{ marginTop: 44, marginBottom: 34 }}
                tabs={tabs}
                autoHeight
                name="overview-tab"
                scrollTabs
                isDisableDropdown
              >
                {JSX}
              </TabView>
            : JSX
          }
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderDocumentsResp() {
    const { productData } = this.state;

    try {
      return _.map(productData.product_details.policy_documents.contents, (content, index) => (
        <Col
          className="d-block d-md-none"
          xs={12}
          key={`document-${index}`}
        >
          <button
            className="document-container"
            onClick={() => {
              window.open(content.value);
            }}
          >
            <div className="document">
              <img src={ProductImages.documentsResp} alt="Bell.svg" />
              <p>{content.title}</p>
            </div>
          </button>
        </Col>
      ));
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderPriceResp() {
    const { productData, isShowBottomPrice } = this.state;
    const { planType } = this.props;

    try {
      const pricePlan = productData.product_details.coverages[planType].premium;

      let price = null;

      if (pricePlan.net_price !== pricePlan.price) {
        // eslint-disable-next-line
        price = pricePlan.price;
      }

      const outerStyle = isShowBottomPrice ? 'bottom-opened-outer' : '';
      const innerStyle = isShowBottomPrice ? 'bottom-opened-inner' : '';

      // d-md-none
      return (
        <Fragment>
          <div
            className={`d-flex ${outerStyle} product-price-resp-outer-container`}
          />
          <div
            className={`d-flex ${innerStyle} product-price-resp-container`}
          >
            <div className="price-container">
              <h5>{productData.info.product_name}</h5>
              <h6>{_.startCase(planType)}</h6>
              <h4>
                <span>${pricePlan.net_price.toFixed(2)}</span>
                {
                  price && <span className="price">${price.toFixed(2)}</span>
                }
              </h4>
            </div>
            <div>
              <Button
                variant="medium-flat-green"
                height={48}
                onClick={this.handleOnClick}
              >
                <p>Buy Policy</p>
              </Button>
            </div>
          </div>
        </Fragment>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    const {
      productData,
      showModal,
      showPrexModal,
      emailPopupData,
    } = this.state;
    const { handleOnClick, handleAPI } = this.props;

    if (!productData) {
      return null;
    }

    let isRespBannerActive = false;
    try {
      isRespBannerActive = productData.product_details.banner_mobile_img;
    } catch (error) {
      // eslint-disable-next-line
    }

    return (
      <Row className="product-detail-container" noGutters>
        {this.renderHeader(isRespBannerActive)}
        {this.renderBanner()}
        {this.renderFeature()}
        {this.renderHighlights()}
        {this.renderDetails(true)}
        {this.renderDetailsResp(true)}
        {this.renderDetails(false)}
        {this.renderDetailsResp(false)}
        {this.renderDocumentsResp()}
        {this.renderPriceResp()}
        <ModalPopup
          showModal={showModal}
          closeModal={this.hideModal}
          className="product-detail-error"
          backdropClassName="blue-modal-backdrop"
          header={<p />}
          body={<p>An error has occured! Inputs might be invalid for this plan. Please try again.</p>}
          footer={
            <Button
              variant="medium-flat-green"
              height={48}
              onClick={this.hideModal}
            >
              <p>Ok</p>
            </Button>
          }
        />
        <RestrictionsPopup />
        <FlowEnquiry
          showModal={showPrexModal}
          hideEnquiryModal={() => this.showEnquiryModal(false)}
          onNext={() => handleOnClick(productData)}
          handleAPI={handleAPI}
          data={emailPopupData}
        />
      </Row>
    );
  }
}

ProductDetails.defaultProps = {
  splitParams: null,
  plansData: null,
  values: null,
  emailPopupData: [],
};

ProductDetails.propTypes = {
  planType: PropTypes.string.isRequired,
  splitParams: PropTypes.object,
  setOverridePlanTypes: PropTypes.func.isRequired,
  doSetRestrictionPopup: PropTypes.func.isRequired,
  plansData: PropTypes.object,
  handleOnClick: PropTypes.func.isRequired,
  values: PropTypes.object,
  switchView: PropTypes.func.isRequired,
  handleAPI: PropTypes.func.isRequired,
  emailPopupData: PropTypes.array,
};

// const mapStateToProps = (state, ownProps) => ({
// });

const mapDispatchToProps = dispatch => ({
  doSetRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default connect(null, mapDispatchToProps)(ProductDetails);
