/**
 * PromoPopup.js
 * popup used to display promos on Form landings
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import TagLabel from 'assets/images/common/TagLabel.svg';

import './VoucherTag.scss';

class VoucherTag extends Component {
  render() {
    const { history, className, teaser } = this.props;

    if (!teaser) {
      return null;
    }

    return (
      <div className={['voucher-tag-container', className].join(' ')}>
        <img src={TagLabel} alt="TagLabel.svg" />
        {
          teaser.is_url && teaser.value ?
            <button
              type="button"
              onClick={() => history.push(teaser.value)}
            >
              {teaser.name}
            </button>
          :
            <span>{teaser.name}</span>
        }
      </div>
    );
  }
}

VoucherTag.defaultProps = {
  className: '',
  teaser: null,
};

VoucherTag.propTypes = {
  className: PropTypes.string,
  history: PropTypes.object.isRequired,
  teaser: PropTypes.object,
};

export default withRouter(VoucherTag);
