/**
 * HeaderDescWithTypeDate.js
 * Product summary with type and date
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col, Tooltip } from 'reactstrap';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import InsurersLogo from 'assets/images/InsurerLogos';
import TooltipImg from 'assets/images/forms/Tooltip.svg';
import LabelImg from 'assets/images/forms/Label.svg';
import TagLabel from 'assets/images/common/TagLabel.svg';

import Button from 'components/FormsV3/Button/Button';
import FlowEnquiry from 'components/Templates/FlowEnquiry/FlowEnquiry';
// import SingleCheckBox from 'components/FormsV3/SingleCheckBox/SingleCheckBox';

import ListViewItemDetails from './ListViewItemDetails/ListViewItemDetails';

import './ListViewItem.scss';

class ListViewItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showTooltips: {},
      showPrexModal: false,
    };

    this.renderCompareTab = this.renderCompareTab.bind(this);
    this.renderInsuranceInfo = this.renderInsuranceInfo.bind(this);
    this.renderInsuranceDetail = this.renderInsuranceDetail.bind(this);
    this.renderPriceBuy = this.renderPriceBuy.bind(this);
    this.renderInsuranceDetailResp = this.renderInsuranceDetailResp.bind(this);

    this.showEnquiryModal = this.showEnquiryModal.bind(this);
  }

  showEnquiryModal(showPrexModal) {
    this.setState({ showPrexModal });
  }

  renderCompareTab() {
    const { planData } = this.props;
    const price = planData.discount ? planData.price : '';

    // const compareStyle = planData.promo ? '' : 'compare-extra';

    return (
      <div className="compare-container w-100 d-flex d-md-block flex-wrap">
        <div className="compare-price w-100 d-md-none">
          <p className="total d-none d-md-block">Total</p>
          <p className="net-price">{planData.netPrice}</p>
          {
            price ?
              <p className="price"><span>{planData.discount}</span><span>{price}</span></p>
            :
              null
          }
        </div>
        {/* {
          planData.promo ?
            <div>
              <span className="d-block d-md-none promo-tag">
                Recommended
              </span>
            </div>
          : null
        }
        <div className={`compare ${compareStyle} d-block d-md-none`}>
          <SingleCheckBox
            id={`checkbox-${planData.productName}`}
            onChange={() => planData.handleCompareCheck(planData.insurerSlug)}
            checked={!!planData.isSelected}
          >
            Compare
          </SingleCheckBox>
        </div> */}
      </div>
    );
  }

  renderInsuranceInfo() {
    const { planData, handleProductPage } = this.props;

    try {
      const image = InsurersLogo[planData.insurerSlug];

      return (
        <div className="insur-container">
          <img className="logo" src={image} alt="InsurerLogo.png" />
          <div className="header-inner">
            <h2>{planData.productName}</h2>
            <button
              type="button"
              onClick={() => handleProductPage(planData.insurerSlug, planData.productSlug)}
            >
              <p>Read more</p>
            </button>
          </div>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderInsuranceDetail() {
    const { showTooltips } = this.state;
    const { planData } = this.props;

    return (
      <div className="detail-container d-none d-md-flex">
        {
          _.reduce(planData.coverages && planData.coverages.highlights, (result, detail, index) => {
            if (index < 3) {
              const tooltip = `tool-tip-${planData.productIndex}-${index}`;

              result.push((
                <div className="detail-inner-wrapper" key={tooltip}>
                  <div className="detail-inner">
                    <div>
                      <p className="detail-title">{detail.title}</p>
                      <p className="detail-value">{detail.value}</p>
                    </div>
                    {
                      detail.tooltip ?
                        <div className="d-flex align-items-start">
                          <Tooltip
                            target={tooltip}
                            isOpen={showTooltips[tooltip]}
                            toggle={() => this.setState({ showTooltips: { ...showTooltips, [tooltip]: false } })}
                            className="animated fade"
                          >
                            {detail.tooltip}
                          </Tooltip>
                          <img
                            id={tooltip}
                            src={TooltipImg}
                            alt="Tooltip.svg"
                            onMouseEnter={() => this.setState({ showTooltips: { ...showTooltips, [tooltip]: true } })}
                          />
                        </div>
                      : null
                    }
                  </div>
                </div>
              ));
            }

            return result;
          }, [])
        }
      </div>
    );
  }

  renderInsuranceDetailResp() {
    const { showTooltips } = this.state;
    const { planData } = this.props;

    return (
      <div className="detail-container d-flex d-md-none">
        <div className="detail-inner">
          <div>
            {
              _.reduce(planData.coverages && planData.coverages.highlights, (result, detail, index) => {
                if (index < 3) {
                  result.push((
                    <p
                      key={`detail-title-${planData.productName}-${index}`}
                      className="detail-title"
                    >
                      {detail.title}
                    </p>
                  ));
                }

                return result;
              }, [])
            }
          </div>
          <div className="detail-price-container">
            {
              _.reduce(planData.coverages && planData.coverages.highlights, (result, detail, index) => {
                if (index < 3) {
                  const tooltip = `tool-tip-small-${planData.productIndex}-${index}`;

                  result.push((
                    <div className="d-flex" key={`detail-price-${planData.productName}-${index}`}>
                      {
                        detail.tooltip ?
                          <Fragment>
                            <Tooltip
                              target={tooltip}
                              isOpen={showTooltips[tooltip]}
                              toggle={() => this.setState({ showTooltips: { ...showTooltips, [tooltip]: false } })}
                              className="animated fade"
                            >
                              {detail.tooltip}
                            </Tooltip>
                            <img
                              id={tooltip}
                              src={TooltipImg}
                              alt="Tooltip.svg"
                              onMouseEnter={() => this.setState({ showTooltips: { ...showTooltips, [tooltip]: true } })}
                            />
                          </Fragment>
                        : null
                      }
                      <p className="detail-value">{detail.value}</p>
                    </div>
                  ));
                }

                return result;
              }, [])
            }
          </div>
        </div>
      </div>
    );
  }

  renderPriceBuy() {
    const { planData } = this.props;

    const price = planData.discount ? planData.price : '';

    // const compareStyle = planData.promo ? '' : 'compare-extra';


    return (
      <Row className="w-100" noGutters>
        <Col className="non-resp-compare-reco" xs={12}>
          {/* <div className={`compare ${compareStyle} ${planData.promo ? '' : 'compare-padding'} d-none d-md-block`}>
            <SingleCheckBox
              id={`checkbox-${planData.productName}`}
              onChange={() => planData.handleCompareCheck(planData.insurerSlug)}
              checked={!!planData.isSelected}
            >
              Compare
            </SingleCheckBox>
          </div>
          {
            planData.promo &&
            <div className="h-100">
              <span className="d-none d-md-block promo-tag">
                Recommended
              </span>
            </div>
          } */}
        </Col>
        <Col className="d-none d-md-flex price-non-resp" xs={12} md={6}>
          <p className="net-price">{planData.netPrice}</p>
          {
            price ?
              <p className="price">
                <span>{planData.discount}</span>
                <span>{price}</span>
              </p>
            :
              null
          }
        </Col>
        <Col className="button-container" xs={12} md={6}>
          <Button
            variant="medium-flat-green"
            height={40}
            onClick={() => {
              if (planData.emailPopupData) {
                this.showEnquiryModal(true);
                return;
              }
              planData.buyCallBack();
            }}
          >
            <p>Buy Policy</p>
          </Button>
          <div className="cashback-earned-container">
            <img src={LabelImg} alt="Label.svg" />
            <p><span>{planData.cashback} Cashback</span></p>
          </div>
        </Col>
      </Row>
    );
  }

  renderEnquiry() {
    const { showPrexModal } = this.state;
    const { planData, handleAPI } = this.props;

    if (!(planData && planData.emailPopupData)) {
      return null;
    }

    return (
      <FlowEnquiry
        showModal={showPrexModal}
        hideEnquiryModal={() => this.showEnquiryModal(false)}
        onNext={() => planData.buyCallBack(true)}
        handleAPI={handleAPI}
        data={planData.emailPopupData}
      />
    );
  }

  render() {
    const { planData, history } = this.props;

    if (!planData) {
      return null;
    }

    return (
      <Row className="header-list-view-container" noGutters>
        <Col md={12} className="info-container w-100 d-none d-md-flex">
          {this.renderCompareTab()}
        </Col>
        <Col xs={12} md={7} className="info-container">
          {this.renderInsuranceInfo()}
          {this.renderInsuranceDetail()}
          {this.renderInsuranceDetailResp()}
        </Col>
        <Col xs={7} className="info-container d-flex d-md-none">
          {this.renderCompareTab()}
        </Col>
        <Col xs={5} md={5} className="price-container">
          {this.renderPriceBuy()}
        </Col>
        {
          planData.teaser &&
          <Col className="tag-label" xs={12}>
            <img src={TagLabel} alt="TagLabel.svg" />
            {
              planData.teaser.is_url && planData.teaser.value ?
                <button
                  type="button"
                  onClick={() => history.push(planData.teaser.value)}
                >
                  {planData.teaser.name}
                </button>
              :
                <span>{planData.teaser.name}</span>
            }
          </Col>
        }
        <Col xs={12}>
          <ListViewItemDetails
            planData={planData}
          />
        </Col>
        {this.renderEnquiry()}
      </Row>
    );
  }
}

ListViewItem.defaultProps = {
  planData: null,
};

ListViewItem.propTypes = {
  planData: PropTypes.object,
  handleProductPage: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  handleAPI: PropTypes.func.isRequired,
};

export default withRouter(ListViewItem);
