import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import Tooltip from 'components/Templates/Tooltip/Tooltip';

class ListViewTierToolTip extends Component {
  render() {
    return (
      <Tooltip
        id="compare_form_tier_tooltip"
        innerClassName="tooltip-wide-modal "
      >
        <p><span className="desc-text">We have made it easier for you to do an apple to apple comparison by classifying the different plans into 3 levels of cover.</span></p>
        <p className=" mt-4"><span className="desc-text">Insurers typically offers 3 levels of cover.</span></p>
        <Row className="w-100" noGutters>
          <Col xs={4}>
            <span className="title-text">Basic:</span>
          </Col>
          <Col xs={8}>
            <span className="desc-text">Lowest protection benefit.</span>
          </Col>
        </Row>
        <Row className="w-100" noGutters>
          <Col xs={4}>
            <span className="title-text">Essential:</span>
          </Col>
          <Col xs={8}>
            <span className="desc-text">Mid-range protection benefit.</span>
          </Col>
        </Row>
        <Row className="w-100" noGutters>
          <Col xs={4}>
            <span className="title-text">Elite:</span>
          </Col>
          <Col xs={8}>
            <span className="desc-text">Comprehensive protection benefit.</span>
          </Col>
        </Row>
      </Tooltip>
    );
  }
}

export default ListViewTierToolTip;
