import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import { connect } from 'react-redux';
// import PropTypes from 'prop-types';
import Sticky from 'react-stickynode';

import DollarLogo from 'assets/images/EditForm/Dollar.svg';
import Arrow from 'assets/images/forms/arrow.svg';

import DropDown from 'components/FormsV3/DropDown/DropDown';
import ChangeInitialFormDataNew from 'components/Templates/ChangeInitialFormData/ChangeInitialFormDataNew';
// import CompareView from 'components/Templates/CompareViewNew/CompareView';
import ProductDetails from 'components/Templates/ProductDetails/ProductDetails';

import { ampTag } from 'lib/tagFunctions';
import { setRestrictionPopup } from 'actions/restrictionsActions';

import ListView from './ListView/ListView';
// import ListCompareBottom from './ListCompareBottom/ListCompareBottom';
import ListViewTierToolTip from './ListViewTierToolTip/ListViewTierToolTip';

const planTypes = ['Basic', 'Essential', 'Elite'];
const viewState = ['list', 'compare', 'product'];

const planTypesList = _.map(planTypes, type => ({ display_name: type, value: type }));

class ListViewMultiCompare extends Component {
  /**
   * Lifecycle Method - constructor
   */
  constructor(props) {
    super(props);

    const {
      splitParams,
    } = this.props;

    this.state = {
      currViewState: splitParams.product && splitParams.insurer ? viewState[2] : viewState[0],
      isFadeOut: false,
      overridePlanTypes: null,
      fadingList: [],
    };

    this.renderLeftContainer = this.renderLeftContainer.bind(this);
    this.renderBackToListButton = this.renderBackToListButton.bind(this);
    // this.setCompareSelected = this.setCompareSelected.bind(this);
    // this.setCancelSelected = this.setCancelSelected.bind(this);
    this.switchView = this.switchView.bind(this);
    // this.scrollPopUpCallback = this.scrollPopUpCallback.bind(this);
    this.setButtonSelected = this.setButtonSelected.bind(this);
    // this.revalidateSelection = this.revalidateSelection.bind(this);
    this.setOverridePlanTypes = this.setOverridePlanTypes.bind(this);
    this.handleTierSelection = this.handleTierSelection.bind(this);
  }

  componentDidMount() {
    const { getPlans, plansData } = this.props;

    // Get list plans
    if (!plansData) {
      getPlans();
    }
  }

  componentDidUpdate(prevProps) {
    const {
      splitParams,
    } = this.props;

    // For product page use
    // If detected is product, goes to product view
    if (!_.isEqual(splitParams, prevProps.splitParams) && splitParams.insurer && splitParams.product) {
      this.switchView(viewState[2]);
    }
  }

  setOverridePlanTypes(overridePlanTypes) {
    const { planType, setPlanType } = this.props;

    const planTypeUpper = _.startCase(planType);

    if (overridePlanTypes &&
      overridePlanTypes.length > 0 &&
      !_.find(overridePlanTypes, p => p.value === planTypeUpper)) {
      const value = _.find(planTypesList, p => p.value === overridePlanTypes[0].value);
      setPlanType(value && value.value);
    }

    this.setState({ overridePlanTypes });
  }

  // setCompareSelected(item) {
  //   const { selectedArray, setSelectedArray } = this.props;
  //   const newSelectedArray = [...selectedArray];

  //   let result = false;

  //   // Null = No empty slot
  //   // -1 = Deselection
  //   // 0,1,2 - Empty slot
  //   let emptySlot = null;

  //   for (let i = 0; i < newSelectedArray.length; ++i) {
  //     if (newSelectedArray[i] === null) {
  //       if (emptySlot === null) {
  //         emptySlot = i;
  //       }
  //     } else if (newSelectedArray[i] === item) {
  //       newSelectedArray[i] = null;
  //       emptySlot = -1;
  //     }
  //   }

  //   // Can select
  //   if (emptySlot !== null && emptySlot >= 0) {
  //     newSelectedArray[emptySlot] = item;
  //     result = true;
  //   }

  //   setSelectedArray(newSelectedArray);

  //   return result;
  // }

  // setCancelSelected(item, isAll = false) {
  //   const { setSelectedArray, selectedArray } = this.props;

  //   if (isAll) {
  //     setSelectedArray([null, null, null]);
  //     return;
  //   }

  //   const newSelectedArray = [...selectedArray];

  //   const index = _.findIndex(newSelectedArray, selected => selected === item);
  //   if (index !== -1) {
  //     newSelectedArray[index] = null;
  //   }

  //   setSelectedArray(newSelectedArray);
  // }

  // Sets the tab selection
  setButtonSelected(index) {
    const { setSelectionButtonIndex } = this.props;
    setSelectionButtonIndex(index);
  }

  // revalidateSelection(planTypeOverwrite = null) {
  //   const {
  //     selectedArray,
  //     setSelectedArray,
  //     plansData,
  //     planType,
  //   } = this.props;

  //   const planTypeToUse = planTypeOverwrite || planType;
  //   const newSelectedArray = [...selectedArray];

  //   for (let i = 0; i < newSelectedArray.length; ++i) {
  //     try {
  //       const product = _.find(plansData.insurances_all, p => p && p.info && p.info.insurer_product_slug === newSelectedArray[i]);
  //       if (!product.policy_info[planTypeToUse].premium) {
  //         newSelectedArray[i] = null;
  //       }
  //     } catch (error) {
  //       // eslint-disable-next-line
  //       newSelectedArray[i] = null;
  //     }
  //   }

  //   setSelectedArray(newSelectedArray);
  // }

  async switchView(state) {
    // const {
    //   currViewState,
    // } = this.state;
    const { category, resetPath } = this.props;

    // if (currViewState !== viewState[0]) {
    //   this.revalidateSelection();
    // }

    this.setState({
      isFadeOut: true,
      overridePlanTypes: null,
    }, async () => {
      await new Promise(resolve => setTimeout(() => this.setState({ currViewState: state }, resolve), 300));
      if (state === viewState[2]) {
        await new Promise(resolve => setTimeout(resolve, 100));
      }

      this.setState({
        isFadeOut: false,
      });

      if (state === viewState[1]) {
        ampTag(`${_.startCase(category)}CompareS`);
      }

      if (state !== viewState[2] && resetPath) {
        resetPath();
      }
    });
  }

  // scrollPopUpCallback(insurerProductSlug, hoveredIndex) {
  //   const { selectedArray, setSelectedArray } = this.props;
  //   const newSelectedArray = [...selectedArray];

  //   newSelectedArray[hoveredIndex] = insurerProductSlug;
  //   setSelectedArray(newSelectedArray);
  // }

  // Change tier
  async handleTierSelection(newPlanType) {
    const { currViewState } = this.state;
    const { plansData, setPlanType } = this.props;

    try {
      if (!this.mutex && newPlanType && plansData && plansData.insurances_all) {
        this.mutex = true;
        let fadingList = [];

        if (currViewState === viewState[0]) {
          for (let i = 0; i < plansData.insurances_all.length; ++i) {
            fadingList.push('list-item-fade');
            this.setState({
              fadingList: [...fadingList],
            });

            // eslint-disable-next-line
            await new Promise((r) => { setInterval(r, 10); });
          }

          // this.setState({
          //   fadingList: [...fadingList],
          // });
          // await new Promise((r) => { setInterval(r, 250); });
          await new Promise((r) => { setInterval(r, 100); });
        }

        setPlanType(newPlanType.toLowerCase());

        if (currViewState === viewState[0]) {
          fadingList = _.map(plansData.insurances_all, () => 'list-item-fade');
          for (let i = 0; i < fadingList.length; ++i) {
            fadingList[i] = '';
            this.setState({
              fadingList: [...fadingList],
            });

            // eslint-disable-next-line
            await new Promise((r) => { setInterval(r, 10); });
          }

          // this.setState({
          //   fadingList: [...fadingList],
          // });
        }

        this.revalidateSelection();

        this.mutex = false;
      }
    } catch (error) {
      this.mutex = false;
      this.setState({
        fadingList: [],
      });
    }
  }

  renderBackToListButton() {
    const { currViewState } = this.state;

    if (currViewState !== viewState[0]) {
      return (
        <button
          className="back-list-button"
          onClick={() => this.switchView(viewState[0])}
        >
          <img src={Arrow} alt="Arrow.svg" />
          Back to Product List
        </button>
      );
    }

    return null;
  }

  renderLeftContainer() {
    const { currViewState, overridePlanTypes } = this.state;
    const {
      editForm,
      form,
      promoGiveAway,
      choosePlanListData,
      selectionButtonIndex,
      planType,
      values,
      getPlans,
    } = this.props;

    let promoJSX = null;
    const selectionButtonsArray = _.map(choosePlanListData || [], data => ({ display_name: data.type, value: data.type }));

    try {
      const currDate = new Date();
      const startDate = new Date(promoGiveAway.main[0].start_at);
      const endDate = new Date(promoGiveAway.main[0].end_at);
      if (startDate <= currDate && currDate <= endDate) {
        promoJSX = (
          <a className="d-none d-lg-block" href={promoGiveAway.main[0].cta_url} target="__blank">
            <img
              className="main-promo"
              src={promoGiveAway.main[0].image_url}
              alt="PromoMain.png"
            />
          </a>
        );
      }
    } catch (error) {
      // eslint-disable-next-line
      // console.log(error);
    }

    const JSX = (
      <Fragment>
        <Row noGutters>
          {
            currViewState !== viewState[0] ?
              <Col className="plantype-drop" style={{ minHeight: 0 }} xs={{ size: 6, order: 3 }} lg={{ size: 12, order: 1 }}>
                {this.renderBackToListButton()}
              </Col>
            : null
          }
          <Col className="plantype-drop d-none d-lg-block" xs={{ size: 6, order: 2 }} lg={{ size: 12, order: 2 }}>
            <div className="tier-selection-checkbox-container">
              <div className="tier-header">
                <p className="my-0 detail-value"><span>Coverage Level</span></p>
                <ListViewTierToolTip />
              </div>
              <h6>Please select one</h6>
              <div className="options-container">
                {
                  _.map(overridePlanTypes || planTypesList, (type, index) => (
                    <div className="option" key={`planType-checkbox-${index}`}>
                      <button
                        className={['option-button', type.value.toLowerCase() === planType ? 'selected' : ''].join(' ')}
                        type="button"
                        onClick={() => this.handleTierSelection(type.value)}
                      />
                      <button
                        type="button"
                        onClick={() => this.handleTierSelection(type.value)}
                      >
                        <span className="detail-value">{type.value}</span>
                      </button>
                    </div>
                  ))
                }
              </div>
            </div>
          </Col>
          <Col className="plantype-drop d-block d-lg-none" xs={{ size: 6, order: 2 }} lg={{ size: 12, order: 2 }}>
            <DropDown
              dropList={overridePlanTypes || planTypesList}
              placeholderString="Coverage Level"
              logo={DollarLogo}
              value={{ display_name: _.startCase(planType), value: _.startCase(planType) }}
              isDisableTyping
              noExternalDesc
              onChange={(type, name, value) => {
                this.handleTierSelection(value.value);
              }}
            />
            {/* <ListViewTierToolTip /> */}
          </Col>
          {
            currViewState === viewState[0] && selectionButtonsArray ?
              <Col xs={{ size: 6, order: 3 }} lg={{ size: 12, order: 3 }} className="d-block d-lg-none filter-container">
                <DropDown
                  dropList={selectionButtonsArray}
                  placeholderString="Filter"
                  isDisableTyping
                  noExternalDesc
                  value={selectionButtonsArray[selectionButtonIndex]}
                  onChange={(type, name, value) => {
                    const index = _.findIndex(selectionButtonsArray, selection => selection.value === value.value);
                    this.setButtonSelected(index);
                  }}
                />
              </Col>
            : null
          }
          <Col xs={{ size: 12, order: 1 }} lg={{ size: 12, order: 4 }}>
            <ChangeInitialFormDataNew
              submitCallback={getPlans}
              form={form}
              headerValue={editForm.headerValue}
              valuesToListen={editForm.valuesToListen}
              conditions={editForm.conditions}
              values={values}
            >
              {editForm.editDetail}
            </ChangeInitialFormDataNew>
          </Col>
        </Row>
        {promoJSX}
      </Fragment>
    );

    return (
      <div className="left-container" >
        <div className="d-none d-lg-block">
          <Sticky
            enabled={true}
            top={100}
            bottomBoundary={0}
          >
            {JSX}
          </Sticky>
        </div>
        <div className="d-block d-lg-none">
          {JSX}
        </div>
      </div>
    );
  }

  /**
   * Render
   */
  render() {
    try {
      const {
        currViewState,
        isFadeOut,
        fadingList,
      } = this.state;

      const {
        plansData,
        // tripType,
        choosePlanListData,
        choosePlanPiggyData,
      } = this.props;

      // Do not render if no data
      if (!plansData) {
        return null;
      }

      const fadeStyle = isFadeOut ? 'animated fadeOut' : 'animated fadeIn';

      return (
        <div className="listview-container d-block d-md-flex justify-content-center">
          {this.renderLeftContainer()}
          <div
            className="right-container"
            ref={node => this.rightContainer = node}
          >
            <div className={`fade-container ${fadeStyle}`}>
              {
                // eslint-disable-next-line
                currViewState === viewState[0] ?
                  <ListView
                    {...this.props}
                    // setCompareSelected={this.setCompareSelected}
                    setButtonSelected={this.setButtonSelected}
                    fadingList={fadingList}
                    listData={choosePlanListData}
                    piggyData={choosePlanPiggyData}
                  />
                // : currViewState === viewState[1] ?
                //   <CompareView
                //     {...this.props}
                //     key="overview"
                //     className="overview-container-padding"
                //     notAccordion
                //     productChangable
                //     scrollPopUpCallback={this.scrollPopUpCallback}
                //     tripType={tripType}
                //   />
                :
                  <ProductDetails
                    {...this.props}
                    setOverridePlanTypes={this.setOverridePlanTypes}
                    switchView={() => this.switchView(viewState[0])}
                  />
              }
            </div>
            {/* <ListCompareBottom
              {...this.props}
              isListView={currViewState === viewState[0]}
              clearAllCallBack={() => this.setCancelSelected(null, true)}
              compareCallback={() => this.switchView(viewState[1])}
              cancelSelectedCallBack={item => this.setCancelSelected(item, false)}
            /> */}
          </div>
        </div>
      );
    } catch (error) {
      return null;
    }
  }
}

