/**
 * HeaderDescWithTypeDate.js
 * Product summary with type and date
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col, Table } from 'reactstrap';
import PropTypes from 'prop-types';

import DetailCollapse from 'components/Templates/DetailCollapse/DetailCollapse';

import './ListViewItemDetails.scss';

class ListViewItemDetails extends Component {
  renderValue(data) {
    if (data.type === 'boolean') {
      return <i className={`fa ${data.value === 'Yes' ? 'fa-check' : 'fa-times'}`} />;
    }

    return data.value;
  }

  render() {
    const { planData } = this.props;

    try {
      if (!planData) {
        return null;
      }

      let subData = [];
      try {
        const leisureActivitiesData = _.find(planData.coverages.coverages, coverage => coverage.header === 'Leisure Activities');
        if (leisureActivitiesData) {
          subData = leisureActivitiesData.contents[0].sub_contents;
        }
      } catch (error) {
        // eslint-disable-next-line
      }

      return (
        <div className="list-view-item-details-container">
          <DetailCollapse topHr>
            <Row className="inner-container" noGutters>
              <Col className="table-container" xs={12} md={6}>
                <h6 className="title">Highlights</h6>
                <Table borderless>
                  <tbody>
                    {
                      _.reduce(planData && planData.coverages && planData.coverages.highlights, (result, highlight, index) => {
                        if (index >= 3) {
                          result.push((
                            <tr key={`${planData.insurerSlug}_highlight_${index}`}>
                              <td className="desc">{highlight.title}</td>
                              <td className="value">{this.renderValue(highlight)}</td>
                            </tr>
                          ));
                        }

                        return result;
                      }, [])
                    }
                  </tbody>
                </Table>
              </Col>
              <Col className="table-container" xs={12} md={6}>
                <h6 className="title">Leisure Activities Covered</h6>
                <Table borderless>
                  <tbody>
                    {
                      _.map(subData, (data, index) => (
                        <tr key={`${planData.insurerSlug}_sub_${index}`}>
                          <td className="desc">{data.title}</td>
                          <td className="value">{this.renderValue(data)}</td>
                        </tr>
                      ))
                    }
                  </tbody>
                </Table>
              </Col>
            </Row>
          </DetailCollapse>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

ListViewItemDetails.defaultProps = {
  planData: null,
};

ListViewItemDetails.propTypes = {
  planData: PropTypes.object,
};

export default ListViewItemDetails;
