/**
 * TravelChoosePlan.js
 * Choose Plan view for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import ListViewMultiCompare from 'components/Templates/ListViewMultiCompareNew/ListViewMultiCompare';
import FieldGenerator from 'components/FormsV3/FieldGenerator';

import TravelEditDetail from './TravelEditDetail';

class TravelChoosePlan extends Component {
  render() {
    const {
      splitParams,
      mainFormFields,
      errors,
      onChangeValue,
      onUpdateValues,
      values,
      valuesToListen,
      conditions,
      validateForm,
      getPlans,
    } = this.props;

    const isXploraJapan = splitParams && splitParams.insurer === 'tokio_marine' && splitParams.product === 'xplora_japan';
    const isPrex = splitParams && _.includes(splitParams.product, 'prex');

    return (
      <ListViewMultiCompare
        {...this.props}
        category="travel"
        form="travel"
        editForm={{
          valuesToListen: valuesToListen(values),
          conditions,
          editDetail: (
            <TravelEditDetail
              isPopUp
              onUpdateValues={onUpdateValues}
              values={values}
              title="Edit Travel Insurance Details"
              validateForm={validateForm}
              onSubmit={getPlans}
            >
              {
                _.reduce(mainFormFields, (result, f, index) => {
                  if (isXploraJapan && (f.slug === 'countries' || f.slug === 'region')) {
                    return result;
                  }
                  result.push((<FieldGenerator
                    key={`business-form-${index}`}
                    values={values}
                    errors={errors}
                    fieldData={f}
                    onChangeValue={(type, name, value) => onChangeValue(type, name, value, mainFormFields)}
                    extraInfo={f.extra_info && <p>{f.extra_info}</p>}
                    noInternalDesc={f.no_internal_desc}
                    isPrex={isPrex}
                  />));

                  return result;
                }, [])
              }
            </TravelEditDetail>
          ),
        }}
        forceFixedOnDesktop
      />
    );
  }
}

TravelChoosePlan.defaultProps = {
  splitParams: null,
};

TravelChoosePlan.propTypes = {
  splitParams: PropTypes.object,
};

export default TravelChoosePlan;
