/**
 * TravelChoosePlanContainer.js
 * The choose plan container for travel
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import document from 'global/document';
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import TravelChoosePlan from 'components/Travel/TravelChoosePlan';

import { setCategoryNavBar } from 'actions/navActions';
import { setInsuranceFormState, FORMSTATES } from 'actions/insuranceActions';
import { parseTravelForm, getTravelPlans, getMainFormFields } from 'actions/travelActions';

import {
  getValuesToListen,
  conditions,
  doTrackingTagsChoosePlan,
  choosePlanListData,
  choosePlanPiggyData,
  msigPrexPopup,
} from './Travel.constants';

import { fbTag, gaTag, ampTag } from '../../lib/tagFunctions';

class TravelChoosePlanContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { splitParams } = prevState;
    const { match } = nextProps;

    if (!_.isEqual(splitParams, match.params)) {
      return {
        splitParams: match.params,
      };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { match } = this.props;
    this.state = {
      splitParams: match.params,
    };

    this.getPlans = this.getPlans.bind(this);
    this.handleOnClick = this.handleOnClick.bind(this);
  }

  async componentDidMount() {
    const {
      setFormState,
      doSetCategoryNavBar,
      mainFormFields,
      handleAPI,
      getMainFields,
      constants,
      history,
    } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);

    fbTag('PPS2 - Pageview - Travel - ChoosePlan', { Product: 'Travel' });
    gaTag({
      page_title: 'Travel - Choose Plan',
      page_path: '/travel/chooseplan',
    });
    ampTag('TravelResultsS');
    document.body.style.backgroundColor = '#F9FAFF';
    doSetCategoryNavBar(true);

    if (!mainFormFields) {
      const mainFields = await handleAPI(
        () => getMainFields(constants),
        false,
        'Unable to get form fields. Please try again!',
        () => history.goBack(),
      );

      if (!mainFields) {
        history.push('/');
      }
    }
  }

  componentWillUnmount() {
    const { doSetCategoryNavBar } = this.props;
    document.body.style.backgroundColor = '#FFFFFF';
    doSetCategoryNavBar(false);
  }

  async getPlans() {
    const {
      values,
      handleAPI,
      setPlansData,
      history,
    } = this.props;

    const parsedParams = parseTravelForm(values);

    // Get plans and valid insurers list
    const newPlansData = await handleAPI(
      () => getTravelPlans(parsedParams),
      false,
      'Unable to get products! Please try again.',
      () => history.goBack(),
    );

    if (newPlansData) {
      setPlansData(newPlansData);
    }
  }

  handleOnClick(product, setRestrictionPopup) {
    const {
      category,
      setFormState,
      setSelectedPlanData,
      planType,
      history,
    } = this.props;

    try {
      doTrackingTagsChoosePlan(planType, product);

      const clickCallback = () => {
        setSelectedPlanData(product);
        setFormState(FORMSTATES.DETAILFORM.index);
        history.push(`/travel${FORMSTATES.DETAILFORM.path}`);
      };

      if (product.restrictions) {
        ampTag(`${_.startCase(category)}RestrictionsS`);

        if (setRestrictionPopup) {
          setRestrictionPopup({
            values: product.restrictions,
            clickCallback,
            doAcceptTag: () => ampTag(`${_.startCase(category)}AcceptA`),
          });
          return true;
        }
      }

      ampTag(`${_.startCase(category)}AcceptA`);
      clickCallback();
      return true;
    } catch (error) {
      // eslint-disable-next-line
    }

    return false;
  }

  render() {
    const { splitParams } = this.state;

    const {
      history,
      values,
      mainFormFields,
    } = this.props;

    if (!mainFormFields) {
      return null;
    }

    let tripType = 'default';
    try {
      if (values.trip_type.value === 'Single Trip') {
        tripType = 'single';
      } else if (values.trip_type.value === 'Annual Multi-Trip') {
        tripType = 'annual';
      }
    } catch (error) {
      // eslint-disable-next-lien
    }

    return (
      <TravelChoosePlan
        {...this.props}
        loadingFailBackState={() => history.goBack()}
        resetPath={() => history.replace(`/travel${FORMSTATES.CHOOSEPLAN.path}`)}
        getPlans={this.getPlans}
        splitParams={splitParams}
        valuesToListen={getValuesToListen}
        conditions={conditions}
        choosePlanListData={choosePlanListData}
        choosePlanPiggyData={choosePlanPiggyData}
        handleOnClick={this.handleOnClick}
        tripType={tripType}
        parseForm={parseTravelForm}
        emailPopupData={[msigPrexPopup]}
      />
    );
  }
}

TravelChoosePlanContainer.defaultProps = {
  values: null,
};

TravelChoosePlanContainer.propTypes = {
  history: PropTypes.object.isRequired,
  values: PropTypes.object,
  constants: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  travelPromoGiveAway: state.insurance.promoGiveaways.travel,
  mainFormFields: state.travel.mainFormFields,
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  getMainFields: constants => dispatch(getMainFormFields(constants)),
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  doSetCategoryNavBar: (isCategory) => {
    dispatch(setCategoryNavBar(isCategory));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TravelChoosePlanContainer));
