/**
* LandingHeader.js
* Landing Header Page - container for advertisement carousel
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import $ from 'jquery';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Button } from 'reactstrap';
import PropTypes from 'prop-types';

import PiggyFillForm from 'components/Templates/PiggySignUp/PiggyFillFormNew';
import RestrictionsPopup from 'components/Templates/RestrictionsPopup/RestrictionsPopup';

import { setRestrictionPopup } from 'actions/restrictionsActions';
import ListViewItem from '../ListViewItem/ListViewItem';

// import css
import './ListView.scss';

class ListView extends Component {
  constructor(props) {
    super(props);

    this.renderListSelectionButtons = this.renderListSelectionButtons.bind(this);
    this.renderList = this.renderList.bind(this);
    this.renderListItem = this.renderListItem.bind(this);
    this.renderPiggy = this.renderPiggy.bind(this);
  }

  componentDidMount() {
    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }
  }

  renderListSelectionButtons() {
    const { listData, setButtonSelected, selectionButtonIndex } = this.props;

    if (listData) {
      return _.map(listData, (data, index) => {
        const selectedStyle = selectionButtonIndex === index ? 'coverage-active-button' : '';
        return (
          <Button
            key={`selection-button-${index}`}
            className={`coverage-button ${selectedStyle}`}
            color="primary"
            onClick={() => setButtonSelected(index)}
          >
            {data.type}
          </Button>
        );
      });
    }

    return null;
  }

  renderList() {
    const {
      listData,
      plansData,
      selectionButtonIndex,
      planType,
    } = this.props;

    try {
      const toMatch = listData[selectionButtonIndex];
      const isFavourite = toMatch.type === 'PolicyPal\'s Favourite';
      const isPrex = toMatch.type === 'Pre-Existing Medical Conditions';

      const result = [];
      const voidIndices = [];
      let keyIndex = 0;
      let lastItem = 0;
      let selectedProducts = [...(isPrex ? plansData.insurances_prex : plansData.insurances)];

      const getNetPrice = product => product &&
        product.policy_info &&
        product.policy_info[planType] &&
        product.policy_info[planType].premium &&
        product.policy_info[planType].premium.net_price;

      try {
        selectedProducts = _.orderBy(selectedProducts, product => getNetPrice(product) || null, ['asc']);
        selectedProducts = _.filter(selectedProducts, product => getNetPrice(product));
      } catch (error) {
        // eslint-disable-next-line
      }

      if (listData) {
        if (isFavourite && toMatch.selection) {
          _.each(toMatch.selection, (data, index) => {
            const insurerIndex = _.findIndex(selectedProducts, product => product && product.info && product.info.insurer_product_slug === data);
            if (insurerIndex !== -1) {
              const isPromo = index === toMatch.selection.length - 1;
              result.push(this.renderListItem(selectedProducts[insurerIndex], insurerIndex, keyIndex, true, isPromo));
              voidIndices.push(insurerIndex);
              keyIndex += 1;
            }
          });
        }
      }

      _.each(selectedProducts, (data, index) => {
        const isValid = _.find(voidIndices, idx => idx === index) === undefined;
        if (isValid) {
          lastItem = index;
        }
      });

      _.each(selectedProducts, (data, index) => {
        const isValid = _.find(voidIndices, idx => idx === index) === undefined;
        let isPromo = false;
        let isEndPromo = false;

        try {
          const indexToMatch = Math.min(selectedProducts.length - 1, 1);
          isPromo = !listData[selectionButtonIndex].selection && index === indexToMatch;
          isEndPromo = index === lastItem;
        } catch (error) {
          // eslint-disable-next-line
        }

        if (isValid) {
          result.push(this.renderListItem(data, index, keyIndex, false, !isEndPromo && isPromo, isEndPromo));
          keyIndex += 1;
        }
      });

      if (_.isEmpty(result)) {
        return <h3 className="text-center mt-5">Sorry! No results for current inputs.</h3>;
      }

      return result;
    } catch (error) {
      // eslint-disable-next-line
    }

    return <h3 className="text-center mt-5">Sorry! No results for current inputs.</h3>;
  }

  renderListItem(data, index, keyIndex, isRecommended = false, isPromo = false, isEndPromo = false) {
    const {
      values,
      promoGiveAway,
      planType,
      // setCompareSelected,
      selectedArray,
      fadingList,
      handleProductPage,
      handleOnClick,
      emailPopupData,
      handleAPI,
    } = this.props;

    let planData = null;
    try {
      const premium = data.policy_info && data.policy_info[planType] && data.policy_info[planType].premium;
      const dataInfo = data.info;

      const promo = isRecommended ? 'recommended' : null;
      const isSelected = _.find(selectedArray, selected => selected === (dataInfo && dataInfo.insurer_product_slug));

      let tripType = 'default';
      if (values.trip_type.value === 'Single Trip') {
        tripType = 'single';
      } else if (values.trip_type.value === 'Annual Multi-Trip') {
        tripType = 'annual';
      }

      const discount = dataInfo &&
      dataInfo.discount &&
      dataInfo.discount[tripType] &&
      dataInfo.discount[tripType].amount > 0 ? ` ${dataInfo.discount[tripType].display} OFF` : null;

      let coverages = null;
      try {
        coverages = data.product_details && data.product_details.coverages[planType];
      } catch (error) {
        // eslint-disable-next-line
      }

      let selectedEmailPopupData = null;
      for (let i = 0; i < emailPopupData.length; ++i) {
        if (dataInfo.insurer_product_slug === emailPopupData[i].insurer_product_slug) {
          selectedEmailPopupData = emailPopupData[i];
          break;
        }
      }

      planData = {
        productIndex: index,
        insurerSlug: dataInfo && dataInfo.insurer_slug,
        productName: dataInfo && dataInfo.product_name,
        productSlug: dataInfo && dataInfo.product_slug,
        wording: dataInfo && dataInfo.wording_url,
        teaser: dataInfo && dataInfo.teaser,
        promo,
        coverages,
        netPrice: premium ? `$${premium.net_price.toFixed(2)}` : '',
        price: premium && premium.price !== premium.net_price ? `$${premium.price.toFixed(2)}` : '',
        cashback: premium && dataInfo ? `$${((dataInfo.cashback || 0) * premium.net_price).toFixed(2)}` : '',
        discount,
        // handleCompareCheck: () => setCompareSelected(dataInfo && dataInfo.insurer_product_slug),
        buyCallBack: noPopup => handleOnClick(data, noPopup ? null : this.props.setRestrictionPopup),
        isSelected,
        emailPopupData: selectedEmailPopupData,
      };
    } catch (error) {
      // console.log(error);
    }

    // console.log(planData);
    let promoJsx = null;
    let promoEndJSX = null;

    try {
      promoJsx = isPromo && promoGiveAway.sub ? this.renderPromoBanner(promoGiveAway.sub[0]) : null;
      promoJsx = isPromo && !promoJsx && promoGiveAway.main ? this.renderPromoBanner(promoGiveAway.main[0], true) : promoJsx;
      promoEndJSX = isEndPromo && !_.isEmpty(promoGiveAway.sub) && promoGiveAway.main ? this.renderPromoBanner(promoGiveAway.main[0], true) : null;
    } catch (error) {
      // eslint-error-next-line
    }

    const fadingStyle = fadingList && keyIndex < fadingList.length ? fadingList[keyIndex] : '';
    return (
      <div
        className="list-item-wrapper"
        key={`listview-item-${keyIndex}`}
      >
        <div className={['list-item', fadingStyle].join(' ')}>
          <ListViewItem
            planData={planData}
            handleProductPage={handleProductPage}
            handleAPI={handleAPI}
          />
          {promoJsx}
          {promoEndJSX}
        </div>
      </div>
    );
  }

  renderPromoBanner(promoData, isMain = false) {
    let promoJsx = null;
    let promoRespJSX = null;

    try {
      const currDate = new Date();
      const startDate = new Date(promoData.start_at);
      const endDate = new Date(promoData.end_at);
      if (!(startDate <= currDate && currDate <= endDate)) {
        return null;
      }
    } catch (error) {
      // eslint-error-next-line
      return null;
    }

    try {
      promoJsx = promoData.image_url && !isMain ? (
        <a className="sub-promo d-none d-lg-flex" href={promoData.cta_url} target="__blank">
          <img
            src={promoData.image_url}
            alt="PromoSub.png"
          />
        </a>
      ) : null;
    } catch (error) {
      // eslint-error-next-line
    }

    try {
      promoRespJSX = promoData.image_url_resp ? (
        <a className="sub-promo d-flex d-lg-none" href={promoData.cta_url} target="__blank">
          <img
            src={promoData.image_url_resp}
            alt="PromoSubResp.png"
          />
        </a>
      ) : null;
    } catch (error) {
      // eslint-error-next-line
    }

    return (
      <Fragment>
        {promoJsx}
        {promoRespJSX}
      </Fragment>
    );
  }

  renderPiggy() {
    const { piggyData, category } = this.props;

    if (!piggyData) {
      return null;
    }

    return (
      <div className="piggy-list-container">
        <PiggyFillForm
          category={category}
          mainText={piggyData.mainText}
          descText={piggyData.descText}
          onClick={piggyData.onClick}
        />
      </div>
    );
  }

  render() {
    return (
      <Fragment>
        <div className="d-none d-lg-flex top-right-container">
          <div className="buttons">
            {this.renderListSelectionButtons()}
          </div>
          {this.renderPiggy()}
        </div>
        <div>
          <div className="d-flex d-lg-none">
            {this.renderPiggy()}
          </div>
          {this.renderList()}
        </div>
        <RestrictionsPopup ref={node => this.resPopup = node} />
      </Fragment>
    );
  }
}

ListView.defaultProps = {
  promoGiveAway: null,
  listData: null,
  selectionButtonIndex: 0,
  piggyData: null,
  fadingList: [],
  values: null,
  plansData: null,
  emailPopupData: null,
};

ListView.propTypes = {
  // setCompareSelected: PropTypes.func.isRequired,
  planType: PropTypes.string.isRequired,
  promoGiveAway: PropTypes.object,
  selectedArray: PropTypes.array.isRequired,
  listData: PropTypes.array,
  setButtonSelected: PropTypes.func.isRequired,
  selectionButtonIndex: PropTypes.number,
  category: PropTypes.string.isRequired,
  piggyData: PropTypes.object,
  fadingList: PropTypes.array,
  handleProductPage: PropTypes.func.isRequired,
  values: PropTypes.object,
  plansData: PropTypes.object,
  handleOnClick: PropTypes.func.isRequired,
  setRestrictionPopup: PropTypes.func.isRequired,
  handleAPI: PropTypes.func.isRequired,
  emailPopupData: PropTypes.array,
};

const mapDispatchToProps = dispatch => ({
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default connect(null, mapDispatchToProps)(ListView);
